.footer__menu{
    display: flex;
}
.footer__menu > div {
    width: 16%;
    margin-bottom: 70px;
}

.footer__menu > div h3 {
    font-size: 18px;
    margin-bottom: 20px;
}

.footer__menu li a{
    font-size: 14px;
    color: #666;
    margin-bottom: 10px;
    display: block;
}

.footer__right{
    border-top: 1px solid #d9d9d9;
    text-align: center;
    padding-top: 40px;
    color: #666;
    line-height: 1.5;
}
@media (max-width:900px){
    .footer__menu{
        flex-wrap: wrap;
    }
}