/* imgTextType */
.imgText__inner{
    display: flex;
    justify-content: space-between;
}

.imgText__inner>div{
    width: 32%;
    height: 500px;
}
.imgText__txt{}
.imgText__txt span{
    font-size: 16px;
    color: #666;
    text-decoration: underline;
    text-underline-position: under;
    margin-bottom: 20px;
}
.imgText__txt h3{
    font-size: 50px;
    font-weight: bold;
    word-break: keep-all;
    line-height: 1.4;
    margin-bottom: 20px;
    margin-top: 20px;
}
.imgText__txt p{
    font-size: 18px;
    font-weight: 300;
    line-height: 1.5;
    color: #666;
    margin-bottom: 10px;
}
.imgText__txt ul{
    font-size: 18px;
    font-weight: 300;
    line-height: 1.6;
}
.imgText__txt ul li{
    position: relative;
    padding-left: 20px;
}

.imgText__txt ul li a{
    color: #666;
}

.imgText__txt ul li::before{
    content: '';
    width: 5px;
    height: 5px;
    border-radius: 50%;
    position: absolute;
    left: 5px;
    top: 9px;
    background: #666;
}

.imgText__img{
    border-radius: 10px;
    position: relative;
}

.imgText__img a{
    position: absolute;
    left: 30px;
    bottom: 30px;
    background-color: #7c2b39;
    color: #fff;
    font-size: 18px;
    padding: 10px 30px;
    border-radius: 30px;
    display: inline-block;
}
.imgText__img a.blue{
    background-color: #2b387c;
}
.imgText__img.img1{
    background: url(../img/imgText_bg01.jpg) no-repeat center / cover;
}
.imgText__img.img2{
    background: url(../img/imgText_bg02.jpg) no-repeat center / cover;
}

@media (max-width: 600px){
    .imgText__inner{
        display: block;
        padding: 20px;
        box-sizing: border-box;
    }
    .imgText__img{
        margin-bottom: 20px;
    }
    .imgText__inner>div{
        width: 100%;
        /* height: 385px; */
    }
    .section:nth-child(3){
        padding: 20px 0;
    }

}